<template>
    <div class="detail">
        <navigate :title="navigateTitle" position="absolute"/>
        <div class="scroll-wrapper">
            <div class="header paper border-1px-b">
                <!--<div class="picture-wrapper border-1px">-->
                <!--<div class="picture" v-lazy:background-image="$images + detail.icon"></div>-->
                <!--</div>-->
                <div class="info" v-cloak>
                    <div class="name" v-if="isNull(detail.name)">{{detail.name}}</div>
                    <div v-if="isNull(detail.bridge_category)">
                        {{detail.type | formatElementName}}类型: {{detail.bridge_category}}
                    </div>
                    <div v-if="isNull(detail.markType)">
                        标志类型: {{detail.markType}}
                    </div>
                    <div v-if="isNull(detail.isShared)">
                        标注类型: {{detail.isShared ? '共享' : '私有'}}
                    </div>
                    <div v-if="isNull(detail.mmsi)">MMSI: {{detail.mmsi}}</div>
                    <div v-if="isNull(detail.beacon_number)">航标编号: {{detail.beacon_number}}</div>
                    <div v-if="isNull(detail.manage_department)">管理部门: {{detail.manage_department}}</div>
                </div>
            </div>
            <div class="button-wrapper paper">
                <mu-button @click="handlerLocation" class="button border-1px-r" flat>
                    <mu-icon class="mr-i" size="16" value="location_on"/>
                    定位
                </mu-button>
                <mu-button :class="detail.isStar ? 'mu-secondary-text-color' : ''" @click="handlerCollection"
                           class="button"
                           flat
                           v-if="visibleCollection">
                    <mu-icon :value="detail.isStar ? 'favorite' : 'favorite_border'"
                             class="mr-i"
                             size="18"/>
                    {{detail.isStar ? '取消收藏' : '收藏'}}
                </mu-button>
            </div>
            <div class="data-wrapper paper" v-cloak>
                <div class="border-1px-b pb-15 fz-16">基本信息</div>

                <!-- 详细内容， 由 contrast 对象生成 -->
                <div v-html="detailGenerate"></div>
                <!-- 详细内容， 由 contrast 对象生成 -->
            </div>
        </div>
    </div>
</template>

<script>
    import Navigate from "../../base/navigate/navigate"
    import NoSuccess from "../../base/no-success/no-success"
    import Toast from 'muse-ui-toast'
    import {deleteIBoatByBoatId, queryILabelDetail, saveOrUpdateIBoat,queryReportDetail} from "../../api/personal"
    import {getNavigationDetail} from "../../api/navigational"
    import {MapMixins} from "../../common/js/mixins"
    import {layerMixins, searchMixins, userMixins} from "../../common/mixins"
    import {detailContrast, contrastFormat} from "../../types/contrast"

    export default {
        name: "detail",
        mixins: [MapMixins, userMixins, layerMixins, searchMixins],
        data() {
            return {
                detail: {}
            }
        },
        props: {
            type: {
                type: String,
                default: ''
            },
            id: {
                type: [String, Number],
                default: ''
            }
        },
        computed: {
            /***
             * 获取 type ID
             * @return {*}
             */
            typeId() {
                return this.$route.query.typeId || this.type
            },
            /***
             * 导航栏标题
             * @return {string}
             */
            navigateTitle() {
                if (this.typeId === '001001') {
                    return '标注详情'
                }
                if (this.typeId === '013019') {
                    return '上报详情'
                }
                const result = this.searchList.find(item => {
                    return item.typeId === this.typeId
                })

                return result ? result.value + '详情' : '详情'
            },
            /***
             * 收藏按钮显示隐藏
             * @return {boolean}
             */
            visibleCollection() {
                return this.typeId === '013001' && !this.$route.query.hideCollection
            },
            detailGenerate() {
                const list = contrastFormat(this.detail, detailContrast[this.typeId])

                return list.map(item => {
                    return `<div class="item">${item.title}: ${item.value}</div>`
                }).join('')
            }
        },
        activated() {
            this.detail = {}

            this.getNavigationDetail()
        },
        methods: {
            // 定位
            handlerLocation() {
                this.$toast({
                    message: '定位中 ...',
                    duration: 0
                })

                this.setCenter(this.detail.longitude, this.detail.latitude).then(res => {
                    const layer = this.getLayer(this.typeId)

                    if (layer && !layer.status) {
                        Toast.message({
                            position: 'bottom',
                            message: `未开启${layer.name}专题图图层`,
                            time: 2500
                        })
                    }

                    this.$router.push('/')
                }).catch(err => {
                    this.$toast({
                        message: err
                    })
                })
            },
            // 收藏
            handlerCollection() {
                if (!this.isLogin) return this.$toast('您需要登录才能收藏船舶! ')

                this.detail.isStar = !this.detail.isStar

                if (this.detail.isStar) {
                    saveOrUpdateIBoat(this.id).then(res => {
                        this.$toast('收藏成功')
                    })
                } else {
                    deleteIBoatByBoatId(this.id).then(res => {
                        this.$toast('取消成功')
                    })
                }
            },
            // 获取详情
            getNavigationDetail() {
                if (this.typeId === '001001') {
                    queryILabelDetail(this.id).then(res => {
                        res.status = ''
                        res.name = res.title

                        this.detail = res
                    })

                    return
                }
                if (this.typeId === '013019') {
                    queryReportDetail(this.id).then(res => {
                        res.status = ''
                        res.name = res.title

                        this.detail = res
                    })

                    return
                }

                const params = {
                    typeId: this.type,
                    id: this.id
                }

                getNavigationDetail(params).then(res => {
                    this.detail = res
                })
            },
            isNull(obj) {
                return !(typeof obj === 'undefined' || obj === null)
            }
        },
        filters: {
            formatElementName(id) {
                const result = this.searchList.find(item => {
                    return item.typeId === id
                })

                return result ? result.value : ''
            }
        },
        components: {
            NoSuccess,
            Navigate
        }
    }
</script>

<style lang="stylus" scoped>
    @import "../../common/stylus/variable.styl"

    .detail
        padding-top 46px
        width 100%
        height 100%
        background-color #fff
        overflow hidden

        .header
            display flex
            padding 15px

            .picture-wrapper
                margin-right 15px
                width 23%

                .picture
                    padding-top 80%
                    width 100%
                    height 0
                    background-color #efefef
                    background-position center center
                    background-size cover

            .info
                flex 1
                min-height 60px
                font-size $font-size-ml
                text-align center

                .name
                    color #3899D7
                    line-height 1.74625
                    font-weight 500

                > *
                    margin-bottom 10px

                    &:last-child
                        margin-bottom 0

        .button-wrapper
            display flex
            align-items center
            margin-bottom 15px

            .button
                flex 1
                height 40px
                line-height 40px

        .data-wrapper
            padding 15px

            >>> .item
                position relative
                padding-left 20px
                min-height 38px
                line-height 38px

                &:before
                    content ''
                    position absolute
                    top 16px
                    left 6px
                    display block
                    width 6px
                    height 6px
                    border-radius 50%
                    background-color #3899D7
</style>



