const MANDEP = '管理单位'

export const contrastFormat = function (instance, contrast) {
    const list = []

    for (let key in contrast) {
        let value = instance[key] ? instance[key] : '', title = contrast[key]

        if (Object.prototype.toString.call(title) === '[object Object]') {
            if (typeof title.format === 'function') {
                value = title.format(value, instance, contrast)
            } else if (typeof title.unit === 'string') {
                value = value ? `${value}${title.unit}` : ''
            }

            title = title.value
        }

        value !== null && list.push({title, value})
    }

    return list
}

